@use '../util' as *;

.cards{
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint-up(medium){
        grid-template-columns: repeat(2, 1fr);
    }
    @include breakpoint-up(large){
        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    flex: 1;
    background: var(--dark-card);
    color: var(--dark-text1);
    //width: rem(255);
    text-align: center;
    padding: rem(25);
    border-radius: rem(5);
    position: relative;
    overflow: hidden;
    transition: background 150ms ease-in-out;
    cursor: pointer;

    &:hover {
        background: var(--dark-card-hover);
    }

    &--facebook {
        border-top: rem(5) solid var(--facebook);
    }

    &--twitter {
        border-top: rem(5) solid var(--twitter);
    }

    &--instagram {
        padding-top: rem(30);
        &::before {
            content: '';
            height: rem(5);
            width: 100%;
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            background: linear-gradient(45deg, var(--instagram-start), var(--instagram-end));
        }
        border-top: rem(5) solid linear-gradient(var(--instagram-start), var(--instagram-end));
    }

    &--youtube {
        border-top: rem(5) solid var(--youtube);
    }

    &--grid {

    }

    &__subtitle {
        font-size: rem(13);
        font-weight: 700;
        
    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: rem(5);
        margin-bottom: rem(28);
        height: rem(20);
    }
     

    &__icon {
        margin-right: rem(8);
        &--facebook {

        }


    }

    &__username {
        font-size: rem(12);
        font-weight: 700;
    }

    &__followers {
        margin-bottom: rem(25);
    }

    &__count {
        color: var(--light-bg);
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);

        &--big {
            font-size: rem(56);
        }

        &--small {
            font-size: rem(32);
        }
    }

    &__label {
        font-size: rem(12);
        font-weight: 400;
        color: var(--dark-text1);
        text-transform: uppercase;
        letter-spacing: rem(3);
        margin-top: rem(9);
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;
        &--up{
            color: var(--limegreen);
        }

        &--down{
            color: var(--brightred);
        }

        img {
            margin-right: rem(4); 
        }
    }

    &__number {
       
    }
}